</template>

<script>
export default {
  components: {},
  data() {
    return {
      swiper: [],
      introduce: [],
      show: false,
      homeList: []
    };
  },
  filters: {
    mianf(val) {
      if (val === 0) {
        return "免费";
      } else {
        return `￥：${(val / 100).toFixed(2)}`;
      }
    }
  },
  name: "Home",
  mounted() {
    this.$http.get("/api/app/banner?").then(res => {
      // console.log(res.data.data);
      this.swiper = res.data.data;
    });
    this.indexList();
    this.godetil();
  },
  methods: {
    async indexList() {
      let { data: res } = await this.$http.get("/api/app/recommend/appIndex?");
      // console.log(res.data);
      this.homeList = res.data;
    },

    // =====跳转详情=====
    async godetil() {
      let Token = localStorage.getItem("adminToken");
      //  console.log(Token)
      if (Token) {
        let { data: res } = await this.$http.get(
          "/api/app/teacher/info/id" + this.$route.params.id
        );
        //  console.log(res)
      } else {
        this.show = true;
      }
    },
    godetils(v) {
      this.$router.push({
        name: "details",
        params: {
          id: v.teacher_id
        }
      });
    },
    goTologin() {
      this.$router.push("/mine");
    },
    goDetails(item) {
      console.log(item);
      this.$router.push({
        name: "details",
        params: {
          mass: item
        }
      });
    },
    xiao() {
      this.show = false;
    },
    icon() {
      this.show = false;
    },
    goKe(v) {
      // console.log(v);
      this.$router.push({
        params: {
          id: v.id,
          type:v.course_type
        },
        name: "kecheng"
      });
    },
    goMine() {
      this.$router.pushO({ name: "MyMine" });
    }
  }
  // mounted() {
  //   this.ajaxSwiper();
  //   this.ajaxList();
  // }
};
</script>
