<template>
  <div class="mistakes_container">
    <!-- 头部部分 -->
    <div class="mistakes_top_container">
      <div>
        <img
          @click="backClick"
          style="width:18px;height:20px;"
          src="data:image/png;base64,"
          alt
        />
      </div>
      <div class="mistakes_top_title">{{this.$route.meta.title}}</div>
      <div>
        <img
          @click="goSearchClick"
          style="width:18px;height:20px;"
          src="data:image/png;base64,"
          alt
        />
      </div>
    </div>
    <!-- 筛选部分 -->
    <div class="mistakes_select_container">
      <div :class="['mistakes_left',{'mistakes_left_active':isShow}]" @click="onClick">
        <div>类型不限</div>
        <van-icon :class="['mistakes_vant',{'mistakes_vant_active':isShow}]" name="play" />
      </div>
      <div class="mistakes_right">
        <div class="right_one">
          <van-icon name="coupon-o" />
          <span>巩固全部错题</span>
        </div>
        <div class="right_two">
          <van-icon name="orders-o" />
          <span>移除错题</span>
        </div>
      </div>
    </div>
    <!-- 提示框部分 -->
    <div class="mistakes_confirm_container" v-show="isShow">
      <div
        :class="['mistakes_confirm_item',{'mistakes_confirm_item_active':selIndex==index}]"
        @click="confirmClick(index)"
        v-for="(item,index) in mistakesData"
        :key="index"
      >{{item}}</div>
    </div>
    <!-- 蒙层部分 -->
    <div class="meng_container" v-show="isShow" @click="closeClick"></div>
    <!-- empty部分 -->
    <div class="mistakes_logo_container">
      <img src="../../assets/empty.png" alt />
      <p>暂无错题记录</p>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      isShow: false,
      selIndex: 0,
      mistakesData: ["全部", "套卷练习", "专点专练", "仿真模考"]
    };
  },
  computed: {},
  methods: {
    onClick() {
      this.isShow = !this.isShow;
    },
    goSearchClick(){
        this.$router.push('/searchs')
    },
    confirmClick(index) {
      this.selIndex = index;
    },
    closeClick() {
      this.isShow = false;
    },
    backClick() {
      window.history.back();
    }
  },
  mounted() {}
};
</script>

<style  scoped>
.mistakes_container {
  width: 100%;
}
.mistakes_top_container {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.mistakes_top_title {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  color: #595959;
}
.mistakes_select_container {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mistakes_left {
  width: 35%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  color: #595959;
}
.mistakes_left_active {
  color: orange;
}
.mistakes_vant {
  transform: rotate(90deg);
  margin-left: 10px;
}
.mistakes_vant_active {
  transform: rotate(-90deg);
  margin-left: 10px;
}
.mistakes_right {
  width: 65%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.right_one {
  width: 45%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: Center;
  color: #8c8c8c;
}
.right_two {
  width: 32%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: Center;
  color: #8c8c8c;
}
.mistakes_logo_container {
  width: 100%;
  height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  color: #595959;
}
.mistakes_confirm_container {
  width: 100%;
  height: 255px;
  position: fixed;
  top: 100px;
  background: #fff;
}
.mistakes_confirm_item {
  width: 100%;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #595959;
}
.mistakes_confirm_item_active {
  color: orange;
}
.meng_container {
  width: 100%;
  height: 400px;
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  bottom: 0px;
}
</style>