<template>
  <div class="detail_da_da">
    <div class="detail_da">
      <div class="iconfont icon-fanhui" @click="onClickLeft"></div>
      <div class="detail_xiang">讲师详情</div>
    </div>
    <!--======= 中间内容======= -->
    <div class="detail_center">
      <div class="detail_center_count">
        <div>
          <div class="detail_center_img">
            <div class="detail_imge">
              <img :src="teacher_introduce.avatar" alt />
            </div>
            <div class="detail_center_name">
              <div class="detail_center_teacher__name">
                {{teacher_introduce.teacher_name}}
                <div class="detail_introduce_level_name">{{teacher_introduce.level_name}}</div>
              </div>

              <div class="detail_center_sex">
                <div class="detail_sex_age">
                  {{teacher_introduce.sex | sex}}
                  <div class="detail_age">{{teacher_introduce.age}}年教龄</div>
                </div>
              </div>
              <div :class="show?'detil_true':'detil_guan'" @click="guanzhu">{{guan}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div>
      <van-tabs title-active-color="orangered" color="orangered" v-model="active">
        <!-- =====讲师介绍====== -->
        <van-tab title="讲师介绍">
          <div class="detil_count">
            <div class="detil_jian" v-if="this.$route.params.id ===3">
              <div v-for="(item,i) in attr" :key="i" class="detil_count">
                <div class="attr_name">{{item.attr_name}}</div>
                <div v-for="(ele,i) in item.attr_value" :key="i">
                  <div class="attr_value_name">{{ele.attr_value_name}}</div>
                </div>
              </div>
            </div>
          </div>

          <!-- ====文卫星==== -->
          <div v-if="this.$route.params.id ===79">
            <div class="wen_da" v-for="(item,i) in wenattr" :key="i">
              <div>{{item.attr_name}}</div>
              <div v-for="(ele,i) in item.attr_value" :key="i">
                <div>{{ele.attr_value_name}}</div>
              </div>
            </div>
          </div>
          <!-- ====马学斌==== -->
          <div v-if="this.$route.params.id ===191">
            <div v-for="(item,i) in maattr" :key="i" class="ma_da">
              <div>{{item.attr_name}}</div>
              <div v-for="(ele,i) in item.attr_value" :key="i">
                <div>{{ele.attr_value_name}}</div>
              </div>
            </div>
          </div>

          <div class="detil_xiangqing">
            <div class="detil_bottom">老师简介</div>
            <div class="detil_jieshao">{{intro}}</div>
          </div>
        </van-tab>
        <!-- =====主课介绍====== -->
        <van-tab title="主讲课程">
          <div>
            <div v-for="(item,i) in list" :key="i" class="list_count">
              <div class="list_title">{{item.title}}</div>
              <div v-for="(ele,i) in item.teachers_list" :key="i">
                <div class="list_image_name">
                  <div class="list_iamge">
                    <img :src="ele.teacher_avatar" />
                  </div>
                  <div class="list_name">{{ele.teacher_name}}</div>
                </div>
              </div>
              <div class="list_num_price">
                <div class="list_num">{{item.sales_num}}人已报名</div>
                <div class="list_price">{{item.price | mian}}</div>
              </div>
            </div>
          </div>
        </van-tab>
        <!-- ======学员评价====== -->
        <van-tab title="学员评价">
          <div class="xue_count_da">
            <div
              class="xue_count"
              v-for="(item,i) in macount"
              :key="i"
            >{{item.title}}({{item.count}})</div>
          </div>
          <div>
            <div v-for="(item,i) in mabottom" :key="i">
              <div class="mabottom_image">
                <img :src="item.avatar" />
              </div>
              <div class="xue_count_bottom">
                <div
                  class="xue_couns"
                  v-for="(item,i) in macount"
                  :key="i"
                >{{item.title}}</div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <!-- ===详情底部=== -->
    <div class="detail_bottom">立即预约</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: "",
      teacher_introduce: [],
      attr: [],
      attrvalue: [],
      intro: [],
      wenattr: [],
      maattr: [],
      list: [],
      macount: [],
      mabottom: [],
      guan:"关注",
      show:false
    };
  },
  mounted() {
    console.log(this.$route.params.id);
    this.$http.get("/api/app/teacher/" + this.$route.params.id).then(res => {
      //   console.log(res.data.data.teacher);
      this.teacher_introduce = res.data.data.teacher;
    });
    this.$http
      .get("/api/app/teacher/info/" + this.$route.params.id)
      .then(res => {
        // console.log(res.data.data.attr);
        // console.log(res.data.data.intro)
        this.attr = res.data.data.attr;
        this.intro = res.data.data.intro;
        this.wenattr = res.data.data.attr;
        this.maattr = res.data.data.attr;
      });
    this.$http
      .post("/api/app/teacher/mainCourse", {
        limit: 10,
        page: 1,
        teacher_id: this.$route.params.id
      })
      .then(res => {
        //   console.log(res.data.data.list)
        this.list = res.data.data.list;
      });
    this.$http.post("/api/app/teacher/comment", {
        limit: 10,
        page: 1,
        teacher_id: this.$route.params.id
      }).then(res => {
        //   console.log(res.data.data.tag)
        console.log(res.data.data.comment.list);
        this.macount = res.data.data.tag;
        this.mabottom = res.data.data.comment.list;
      });
  },
  filters: {
    sex(val) {
      if (val === 0) {
        return "男";
      }
    },
    mian(val) {
      if (val === 0) {
        return "免费";
      } else {
        return `￥：${(val / 100).toFixed(2)}`;
      }
    }
  },
  methods: {
    onClickLeft() {
      this.$router.push({ name: "Home" });
    },
    guanzhu(){
        this.$http.get(`/api/app/teacher/collect/${this.$route.params.id}`).then((res)=>{
          // console.log(res.data.data)
            if(res.data.data.flag === 1){
                this.guan = "已关注"
                this.show = true
            }else{
              this.guan = "关注"
                this.show = false
            }
        })
    }
  }
};
</script>
