<template>
  <div class="course_container">
    <!-- 头部 -->
    <div class="course_top_contaier">
      <div class="course_top_left">特色课</div>
      <div class="course_top_right">
        <img src="../assets/sousuo.png" class="course_top_right_icon" v-on:click="toSearch" alt />
      </div>
    </div>

    <!-- 筛选栏 -->
    <div class="course_sort_container">
      <div class="nav_da">
        <van-dropdown-menu class="te_count">
          <!-- =====分类====== -->
          <div>
            <van-dropdown-item title="分类" ref="ok">
              <div class="fen_da">
                <p>学科</p>
                <div>
                  <div v-for="(item,i) in fenxue" :key="i" class="fen_jie">
                    <div
                      :class="['fen_count',{'active':activenXueindex==i}]"
                      @click="classifyClick(i)"
                    >{{item.name}}</div>
                  </div>
                </div>

                <p>班级</p>
                <div v-for="(v,index) in fenban" :key="index" class="fen_jie">
                  <div
                    :class="['fen_count',{'active':activenBanindex==index}]"
                    @click="gradeClick(index)"
                  >{{v.name}}</div>
                </div>

                <div class="pop_left_footer">
                  <div class="pop_left_footer_reset" @click="chongzhi">重置</div>
                  <div @click="submit" class="pop_left_footer_commit">提交</div>
                </div>
              </div>
            </van-dropdown-item>
          </div>

          <!-- ======排序====== -->
          <van-dropdown-item title="排序" ref="ok">
            <div class="fen_da">
              <div class="pai_count">
                <div class="pai_zong" @click="zong(i)" v-for="(item,i) in zonghe" :key="i">
                  <div @click="sort(i)" :class="storindex === i?'active':''">{{item.title}}</div>
                </div>
              </div>
            </div>
          </van-dropdown-item>

          <!--===== 筛选===== -->
          <van-dropdown-item title="筛选">
            <div class="sai_da">
              <div v-for="(item,i) in saixuan" :key="i" class="sai_count">
                <div class="sai_name">{{item.name}}</div>
              </div>
            </div>
          </van-dropdown-item>
        </van-dropdown-menu>
      </div>
    </div>

    <!-- 主体部分 -->
    <div class="zhu_da">
      <van-list
        :immediate-check="false"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-cell>
          <div v-for="(item,i) in telist" :key="i" class="zhu_count_list">
            <div class="zhu_title">{{item.title}}</div>
            <div class="zhu_time">
              <div class="zhu_day">{{item.end_play_date | day}}</div>
              <div class="zhu_ke">共{{item.total_periods}}课时</div>
            </div>
            <!-- =====老师头像====== -->
            <div v-for="(item,i) in item.teachers_list" :key="i">
              <div class="zhu_img_name">
                <div class="zhu_img">
                  <img :src="item.teacher_avatar" />
                </div>
                <div class="zhu_name">{{item.teacher_name}}</div>
              </div>
            </div>
            <div class="zhu_num_mian">
              <div class="zhu_num">{{item.sales_num}}人已报名</div>
              <div class="zhu_mian">{{item.price | mianf}}</div>
            </div>
          </div>
        </van-cell>
      </van-list>
    </div>

    <!-- 蒙层 -->
    <div class="pop_container" v-show="popShow">
      <!-- 左边弹出层 -->
      <div class="pop_content_left" v-show="popLeft">
        <div v-for="(item,index) in popLeftItems" :key="index">
          <div class="pop_content_left_items">{{item.secondtitle}}</div>
          <div class="pop_content_left_items_list">
            <div
              class="pop_content_left_items_list_items"
              v-for="(items,index) in item.classify"
              :key="index"
            >
              <div>{{items.name}}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 中间弹出层 -->
      <div class="pop_content_middle" v-show="popMiddle">
        <div
          class="pop_content_middle_items"
          v-for="(item,index) in popMiddleItems.classify"
          :key="index"
        >{{item.name}}</div>
      </div>
      <!-- 右边弹出层 -->
      <div class="pop_content_right" v-show="popRight">
        <div class="pop_content_right_container">
          <div
            class="pop_content_left_items_list_items"
            v-for="(item,index) in popRightItems"
            :key="index"
          >{{item.name}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  components: {},
  data() {
    return {
      courseInfo: {
        page: 1,
        limit: 10,
        course_type: 0,
        classify_id: "",
        order_by: "",
        attr_val_id: 1,
        is_vip: 0
      },
      storindex: -1,
      activenXueindex: -1,
      activenBanindex: -1,
      count: 0,
      sorts: [],
      popLeftItems: [],
      popMiddleItems: [],
      popRightItems: [],
      telist: [],
      fenxue: [],
      fenban: [],
      showDown: true,
      showUp: false,
      show: false,
      popShow: false,
      popLeft: false,
      popMiddle: false,
      popRight: false,
      list: [],
      loading: false,
      finished: false,
      saixuan: [
        { name: "全部" },
        { name: "大班课" },
        { name: "小班课" },
        { name: "公开课" },
        { name: "点播课" },
        { name: "面授课" },
        { name: "音频课" },
        { name: "系统课" },
        { name: "图文课" },
        { name: "会员课" }
      ],
      zonghe: [
        { title: "综合评价" },
        { title: "最新" },
        { title: "最热" },
        { title: "从低到高" },
        { title: "从高到低" }
      ]
    };
  },
  computed: {},
  filters: {
    day(val) {
      let date = new Date(parseInt(val) * 1000);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y + "-" + m + "-" + d + " " + h + ":" + minute;
    },
    mianf(val) {
      if (val === 0) {
        return "免费";
      } else {
        return `￥：${(val / 100).toFixed(2)}`;
      }
    }
  },
  methods: {
    sort(i) {
      this.storindex = i;
      switch (i) {
        case 0:
          this.courseInfo.order_by = 0;
          this.$http
            .get("/api/app/courseBasis", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data.data.list);
              this.telist = res.data.data.list;
            });
          break;
        case 1:
          this.courseInfo.order_by = 1;
          this.$http
            .get("/api/app/courseBasis", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data.data.list);
              this.telist = res.data.data.list;
            });
          break;
        case 2:
          this.courseInfo.order_by = 2;
          this.$http
            .get("/api/app/courseBasis", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data.data.list);
              this.telist = res.data.data.list;
            });
          break;
        case 3:
          this.courseInfo.order_by = 3;
          this.$http
            .get("/api/app/courseBasis", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data.data.list);
              this.telist = res.data.data.list;
            });
          break;
        case 4:
          this.courseInfo.order_by = 4;
          this.$http
            .get("/api/app/courseBasis", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data.data.list);
              this.telist = res.data.data.list;
            });
          break;
      }
      // 下拉消失
      this.$refs.ok.toggle();
    },
    classifyClick(i) {
      this.activenXueindex = i;
    },
    gradeClick(i) {
      // console.log(i)
      this.activenBanindex = i;
    },
    toSearch(){
      console.log(1111)
      this.$router.push({name:"searchs"});
    },
    // ==========重置按钮======
    chongzhi() {
      this.courseInfo.attr_val_id = "";
      this.$http
        .get("/api/app/courseBasis", {
          params: this.courseInfo
        })
        .then(res => {
          console.log(res.data.data.list);
          // res.data.data.list.forEach(e => {
          //   this.telist.push(e);
          // });
          this.telist = res.data.data.list;
        });
      // 下拉消失
      this.$refs.ok.toggle();
    },
    // =====确定隐藏======
    submit() {
      // 收集数据Id
      let arr = [];
      if (this.activenXueindex != -1) {
        let xueId = this.fenxue[this.activenXueindex].id;
        arr.push(xueId);
      }
      if (this.activenXueindex != -1) {
        let banId = this.fenban[this.activenXueindex].id;
        arr.push(banId);
      }
      this.courseInfo.attr_val_id = arr.join(",");
      console.log(this.courseInfo.attr_val_id);
      this.courseInfo.page = 1;
      // =======更新list========
      this.$http
        .get("/api/app/courseBasis", {
          params: this.courseInfo
        })
        .then(res => {
          console.log(res.data.data.list);
          // res.data.data.list.forEach(e => {
          //   this.telist.push(e);
          // });
          this.telist = res.data.data.list;
        });
      // 下拉消失
      this.$refs.ok.toggle();

      arr.push();
    },
    courseDetails(item) {
      // console.log(item);
      this.$router.push({
        path: "/courseDetails",
        query: {
          courses: item
        }
      });
    },
    leftClick() {
      this.popShow = !this.popShow;
      this.popLeft = !this.popLeft;
    },
    middleClick() {
      this.popShow = !this.popShow;
      this.popLeft = false;
      this.popMiddle = !this.popMiddle;
    },
    rightClick() {
      this.popShow = !this.popShow;
      this.popRight = !this.popRight;
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        ++this.courseInfo.page;
        this.$http
          .get("/api/app/courseBasis?page=1&limit=10&", {
            params: this.courseInfo
          })
          .then(res => {
            this.telist = this.telist.concat(res.data.data.list);
            console.log(this.telist);
            // 加载状态结束
            this.loading = false;

            // 数据全部加载完成
            if (res.data.data.list.length == 0) {
              this.finished = true;
            }
          });
      }, 1500);
    },
    zong(i) {
      console.log(i);
    }
  },
  mounted() {
    document.documentElement.scrollTop = 0;
    this.$http.get("/api/app/courseClassify").then(res => {
      this.fenxue = res.data.data.attrclassify[0].child;
      this.fenban = res.data.data.attrclassify[1].child;
    });
    this.$http.get("/api/app/courseBasis?page=1&limit=10&").then(res => {
      // console.log(res.data.data.list);
      this.telist = res.data.data.list;
    });
  }
};
</script>
