<template>
  <div class="appraisal_container">
    <!-- 头部部分 -->
    <div class="appraisal_top_container">
      <div>
        <img
          @click="backClick"
          style="width:18px;height:20px;"
          src="data:image/png;base64,"
          alt
        />
      </div>
      <div class="appraisal_top_title">{{this.$route.meta.title}}</div>
      <div>
        <img
          @click="goSearchClick"
          style="width:18px;height:20px;"
          src="data:image/png;base64,"
          alt
        />
      </div>
    </div>
    <!-- 筛选部分 -->
    <div class="appraisal_select_container">
      <div :class="['appraisal_left',{'appraisal_left_active':leftShow}]" @click="leftClick">
        <div>类型不限</div>
        <van-icon :class="['appraisal_vant',{'appraisal_vant_active':leftShow}]" name="play" />
      </div>
      <div :class="['appraisal_right',{'appraisal_right_active':rightShow}]" @click="rightClick">
        <div>时间</div>
        <van-icon :class="['appraisal_vant',{'appraisal_vant_active':rightShow}]" name="play" />
      </div>
    </div>
    <!-- 提示框部分 -->
    <div class="left_confirm_container" v-show="leftShow">
      <div :class="['left_confirm_item',{'left_confirm_item_active':selIndex==index}]" @click="leftItemClick(index)" v-for="(item, index) in left" :key="index">{{item}}</div>
    </div>
    <div class="right_confirm_container" v-show="rightShow">
      <div :class="['right_confirm_item',{'right_confirm_item_active':selIndex==index}]" @click="rightItemClick(index)" v-for="(item,index) in right" :key="index">{{item}}</div>
    </div>
    <!-- 蒙层部分 -->
    <div class="meng_container" v-show="leftShow" @click="closeClick"></div>
    <div class="meng_container" v-show="rightShow" @click="closeClick"></div>
    <!-- empty部分 -->
    <div class="appraisal_logo_container">
      <img src="../../assets/empty.png" alt />
      <p>该时段您没有留下测评记录</p>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      leftShow: false,
      rightShow: false,
      selIndex:0,
      left: ["全部", "套卷练习", "仿真模考", "专点专练"],
      right: ["全部", "今天", "7天"]
    };
  },
  computed: {},
  methods: {
    leftClick() {
      this.leftShow = !this.leftShow;
      this.rightShow=false;
    },
    rightClick() {
      this.rightShow = !this.rightShow;
      this.leftShow=false;
    },
    leftItemClick(index){
        this.selIndex=index;
    },
    rightItemClick(index){
        this.selIndex=index;
    },
    backClick() {
      window.history.back();
    },
    closeClick(){
        this.leftShow=false;
        this.rightShow=false;
    },
    goSearchClick() {
      this.$router.push("/searchs");
    }
  },
  mounted() {}
};
</script>
<style  scoped>
.appraisal_container {
  width: 100%;
}
.appraisal_top_container {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.appraisal_top_title {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  color: #595959;
}
.appraisal_select_container {
  width: 100%;
  height: 45px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.appraisal_left {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #595959;
}
.appraisal_left_active
 {
  color: orange;
}
.appraisal_right_active{
    color:orange;
}
.appraisal_right {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.appraisal_vant {
  transform: rotate(90deg);
}
.appraisal_vant_active {
  transform: rotate(-90deg);
}
.left_confirm_container {
  width: 100%;
  height: 172px;
  position: fixed;
  top: 100px;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.left_confirm_item {
  width: 100%;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #595959;
}
.left_confirm_item_active {
  color: orange;
}
.right_confirm_container {
  width: 100%;
  height: 160px;
  position: fixed;
  top: 100px;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.right_confirm_item {
  width: 100%;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #595959;
}
.right_confirm_item_active {
  color: orange;
}

.meng_container {
  width: 100%;
  height: 400px;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0.5;
  position: fixed;
  bottom: 0px;
}
.appraisal_logo_container {
  width: 100%;
  height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  color: #595959;
}
</style>